import React from "react";
import Header from "../components/header.js"


export default function home_page() {
    function calcAge(dateString) {
        var birthday = +new Date(dateString);
        return ~~((Date.now() - birthday) / 31557600000);
    }

    let age = calcAge("1995-06-14");

    return (
        <>
            <Header/>
            <div id="content">
                <div class="section profile-content">
                    <div class="container">
                        <div class="owner">
                            <div class="avatar">
                                <img
                                    src={require("../assets/img/faces/profilovka.jpg")}
                                    alt="Circle Image"
                                    class="img-circle img-no-padding img-responsive"
                                />
                            </div>
                            <div class="name">
                                <h3 class="title">
                                    Christian Ščípa
                                    <br/>
                                </h3>
                                <h6 class="description">IT STUDENT</h6>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-6 ml-auto mr-auto text-center">
                                <p>
                                    Volám sa Christian, mám {age} rokov a som študent vysokej školy
                                    II. stupňa na Fakulte Informatiky a Informačných Technológií v
                                    Bratislave. Táto stránka slúži ako osobný profil a zároveň ako
                                    portfólio na moje projekty na ktorých som pracoval alebo pracujem.
                                    Takisto sa tu nachádzajú informácie o mojich záľubách - prevažne o
                                    filmoch, seriáloch, knihách a hudbe
                                </p>
                                <br/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
